import React, {useState, useEffect} from 'react';
import { getOther } from '../redux/actions/app'
import { connect } from 'react-redux'
import * as appActions from '../redux/actions/app'
import { bindActionCreators } from 'redux'
import { stringifyDate, stringifyTime, validMail, validPhone } from '../redux/constants/other'
import Render from '../component/Render/Render'
import { getSecure } from '../redux/actions/app'
import { month } from '../redux/constants/other'
import { mainWindow } from '../App'
import { getElsom, getBalanceKg } from '../redux/actions/app'
import sha from 'js-sha1';

const Event =  React.memo(
    (props) =>{
        let [name, setName] = useState('');
        let handleName =  (event) => {
            setName(event.target.value)
        };
        let [email, setEmail] = useState('');
        let handleEmail =  (event) => {
            setEmail(event.target.value)
        };
        let [phone, setPhone] = useState('');
        let handlePhone =  (event) => {
            setPhone(event.target.value)
        };
        let [wallet, setWallet] = useState('');
        const { lang, city } = props.app;
        const { authenticated } = props.user;
        let [date, setDate] = useState(new Date());
        let [selectedDate, setSelectedDate] = useState(undefined);
        let [seances, setSeances] = useState([]);
        let _date = new Date()
        let [cart, setCart] = useState([]);
        let [fullPrice, setFullPrice] = useState(0);
        let [seance, setSeance] = useState(undefined);
        let dates = [new Date(), new Date(_date.setDate(_date.getDate() + 1)), new Date(_date.setDate(_date.getDate() + 1)), new Date(_date.setDate(_date.getDate() + 1)), new Date(_date.setDate(_date.getDate() + 1))];
        let [showRender, setShowRender] = useState({});
        let [movie, setMovie] = useState({});
        let holderSeats = async (seats, seance)=>{
                setSeance(seance);
                let _cart = cart;
                let index = -1;
                _cart.find((element, position)=>{
                    if(JSON.stringify(element)===JSON.stringify(seats))
                        index = position
                });
                if (index===-1)
                    _cart.push(seats);
                else
                    _cart.splice(index, 1);
                let _fullPrice = 0;
                for(let i=0; i<_cart.length; i++){
                    _fullPrice+=parseInt(_cart[i].priceSelect)
                }
                setFullPrice(_fullPrice)
                setCart(_cart)
        };
        useEffect(async ()=>{
            let data = await getOther({name: 'КиноПоНазванию', data: {name: props.location.pathname.split('/')[2]}});
            if(data==undefined){
                props.history.push('/');
                window.location.reload();
            }
            await setMovie(data)
            if(authenticated){
                let data = await getSecure({name: 'Профиль'})
                setEmail(data.email)
                setName(data.name+' '+data.surname)
                setPhone(data.phonenumber)
            }
        },[]);
        useEffect(async ()=>{
            if(movie.name!==undefined){
                if(selectedDate.cinema!==undefined){
                    let _showRender = showRender
                    let keys = Object.keys(_showRender)
                    for(let i=0; i<keys.length; i++) {
                        _showRender[keys[i]] = false
                    }
                    _showRender[selectedDate.cinema] = true
                    setShowRender(_showRender)
                    let data = await getOther({name: 'СеансПоДате', data: {movie: movie._id, cinema: selectedDate.cinema, realDate: selectedDate.date}});
                    setSeance(data)
                } else {
                    setSeance(undefined)
                }
                setCart([])
                setFullPrice(0)
            }
        },[selectedDate]);
        useEffect(async ()=>{
            if(movie.name!==undefined){
                let data = await getOther({name: 'КиноПоДате', data: {movie: movie._id, realDate: date}});
                setSeances(data)
                let _showRender = {}
                for(let i=0; i<data.length; i++){
                    _showRender[data[i].name] = false
                }
                setShowRender(_showRender)
                setSelectedDate({})
            }
        },[date]);
        let buy = async ()=>{
            let seats = [];
            let _seance = seance;
            for (let i = 0; i < _seance.seats.length; i++) {
                for (let i1 = 0; i1 < _seance.seats[i].length; i1++) {
                    if(_seance.seats[i][i1].status==='hold')
                        _seance.seats[i][i1].status='sold'
                }
            }
            let date = selectedDate.date.split('T')[0].split('-')
            let time = selectedDate.date.split('T')[1].split(':')
            let dateTime = date[2]+' '+month[date[1]]+' '+date[0]+', '+time[0]+':'+time[1];
            for(let i =0; i<cart.length; i++){
                cart[i].date = dateTime
                seats[i] = cart[i]
            }
            await getSecure({name: 'КупитьКино', data: {image: movie.image, event: _seance, movie: movie.name, cinema: _seance.cinema.split('|')[0], hall: _seance.cinema.split('|')[1], fullPrice: fullPrice, seats: seats, wallet: wallet, service: merchant, name: name, email: email, phone: phone }})
            if(merchant==='Balance.kg'){
                window.location=elsomCode
            } else if(merchant!=='Visa/Maestro'){
                await props.history.push('/')
                await window.location.reload()
            }
        }
        let [show, setShow] = useState(false);
        let handleShow =  async() => {
            if(show==false){
                let data = await getOther({name: 'КодПлатежа'});
                setWallet(data)
            }
            setMerchant('')
            setElsomCode('')
            setShow(!show)
        };
        let [elsomCode, setElsomCode] = useState('');
        let [visaCode, setVisaCode] = useState('');
        let getVisaCode = async ()=>{
            let a = '170000005'+wallet+fullPrice+'https://kassir.kg/payment/visa/pay'+'https://kassir.kg/payment/tested'+'Auth'+'daseron'+'KYRGYZ17'
            a = sha(a)
            a = new Buffer(a, 'hex')
            a = a.toString('base64')
            setVisaCode(a)
        }
        let [merchant, setMerchant] = useState('');
        let handleMerchant =  (event) => {
            setMerchant(event.target.value)
            if(event.target.value==='Balance.kg')
                getBalance(fullPrice, wallet)
            else if(event.target.value==='ЭЛСОМ')
                getElsomCode(fullPrice, wallet)
            else if(event.target.value==='Visa/Maestro')
                getVisaCode()
            else
                setElsomCode('')
        };
        let getElsomCode = async (sum, wallet)=>{
            let data = await getElsom({sum: sum, wallet: wallet})
            if(data==undefined)
                data = 'error'
            setElsomCode(data)
        }
        let getBalance = async (sum, wallet)=>{
            let data = await getBalanceKg({sum: sum, wallet: wallet})
            if(data==undefined)
                data = 'error'
            setElsomCode(data)
        }
        return (
            <>
            <div className='main-buy kino-detail'>
                <div className='film-detail-wrap'>
                    <div className='container'>
                        <div className='row'>
                            <div className='col-12 trailer-wrap'>
                                <div className='trailer'>
                                    <div style={{height: '85px'}}/>
                                    <div className='trailer-video-wrap' id='trailer-video' style={{display: 'none'}}>
                                        <video width='100%' controls id='trailer-video-18891' className='trailer-video' src='https://api.kinohod.ru/o/3f/32/3f324763-9611-45c7-9fbc-aa57e8771237.mp4' poster='https://api.kinohod.ru/c/900x450/8d/c4/8dc494f8-ad0b-4505-bec7-d86e76a0fa6b.jpg' />
                                    </div>
                                    <div className='trailer_description'>
                                        <div className='trailer_title'>
                                            <h1 className='name'>{movie.name}</h1>
                                            <div className='genre'>{movie.genre}</div>
                                            <div className='age'>{movie.ageCategory}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className='film-detail'>
                            <div className='row'>
                                <div className='col-3'>
                                    <div className='film-detail_image'>
                                        <img src={movie.imageThumbnail} onLoad={(e)=>{if(e.target.src.includes('thumbnail')){e.target.src=movie.image}}} alt />
                                    </div>
                                </div>
                                <div className='col-5'>
                                    <div className='film-detail_info'>
                                        <table className='table'>
                                            <tbody>
                                            {mainWindow.current.offsetWidth>700?
                                                <>
                                                <tr>
                                                    <td className='name'>Длительность:</td>
                                                    <td className='prop'>{movie.duration}</td>
                                                </tr>
                                                <tr>
                                                    <td className='name'>Премьера:</td>
                                                    <td className='prop'>{movie.premier}</td>
                                                </tr>
                                            <tr>
                                                <td className='name'>Режиссёры:</td>
                                                <td className='prop'>{movie.producers}</td>
                                            </tr>
                                            <tr>
                                                <td className='name'>В ролях:</td>
                                                <td className='prop'>{movie.actors}</td>
                                            </tr>
                                                </>
                                                :null}
                                            </tbody></table>
                                    </div>
                                </div>
                                {mainWindow.current.offsetWidth>700?
                                     <div className='col-4'>
                                        <div className='film-detail_description'>
                                            <p>{movie.description}</p>
                                        </div>
                                    </div>
                                    :null}
                            </div>
                        </div>
                    </div>
                </div>
                <div className='schedule-wrap'>
                    <div className='container'>
                        <div className='row'>
                            <div className='col-12'>
                                <div className='schedule'>
                                    <div className='schedule_filter-section'>
                                        <div className='schedule_title'>
                                            <h2>Расписание сеансов</h2>
                                        </div>
                                        <div className='schedule_days-list'>
                                            {mainWindow.current.offsetWidth>700?
                                            <div className='row'>
                                                <div className='col-2'>
                                                    <a className={date===dates[0]?'day-item js-day-item active':'day-item js-day-item'} onClick={()=>{setDate(dates[0])}}>
                                                        <span className='date'>{stringifyDate(dates[0])}</span>
                                                        Сегодня
                                                    </a>
                                                </div>
                                                <div className='col-2'>
                                                    <a className={date===dates[1]?'day-item js-day-item active':'day-item js-day-item'} onClick={()=>{setDate(dates[1])}}>
                                                        <span className='date'>{stringifyDate(dates[1])}</span>
                                                        Завтра
                                                    </a>
                                                </div>
                                                <div className='col-2'>
                                                    <a className={date===dates[2]?'day-item js-day-item active':'day-item js-day-item'} onClick={()=>{setDate(dates[2])}}>
                                                        <span className='date'>{stringifyDate(dates[2])}</span>
                                                    </a>
                                                </div>
                                                <div className='col-2'>
                                                    <a className={date===dates[3]?'day-item js-day-item active':'day-item js-day-item'} onClick={()=>{setDate(dates[3])}}>
                                                        <span className='date'>{stringifyDate(dates[3])}</span>
                                                    </a>
                                                </div>
                                                <div className='col-2'>
                                                    <a className={date===dates[4]?'day-item js-day-item active':'day-item js-day-item'} onClick={()=>{setDate(dates[4])}}>
                                                        <span className='date'>{stringifyDate(dates[4])}</span>
                                                    </a>
                                                </div>
                                            </div>
                                                :<>
                                                <div className='row'>
                                                    <div className='col-2'>
                                                        <a className={date===dates[0]?'day-item js-day-item active':'day-item js-day-item'} onClick={()=>{setDate(dates[0])}}>
                                                            <span className='date'>{stringifyDate(dates[0])}</span>
                                                            Сегодня
                                                        </a>
                                                    </div>
                                                </div>
                                                <div className='row'>
                                                    <div className='col-2'>
                                                        <a className={date===dates[1]?'day-item js-day-item active':'day-item js-day-item'} onClick={()=>{setDate(dates[1])}}>
                                                            <span className='date'>{stringifyDate(dates[1])}</span>
                                                            Завтра
                                                        </a>
                                                    </div>
                                                </div>
                                                <div className='row'>
                                                    <div className='col-2'>
                                                        <a className={date===dates[2]?'day-item js-day-item active':'day-item js-day-item'} onClick={()=>{setDate(dates[2])}}>
                                                            <span className='date'>{stringifyDate(dates[2])}</span>
                                                            Послезавтра
                                                        </a>
                                                    </div>
                                                </div>
                                                </>}
                                        </div>
                                        <div className='map'>
                                            <div className='row'>
                                                <div className='col-12'>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='cinema-list-container'>
                    {seances.map((element, idx) => {
                        return (
                            <>
                            <div className='cinema-row'>
                                <div className='container'>
                                    <div className='place'>
                                        <div className='name'>
                                            <div className='title'>
                                                <a className='cinema-icon' />
                                                <a>{element.name.split('|')[0]}</a>
                                            </div>
                                        </div>
                                        <div className='address'>
                                            {element.name.split('|')[1]}
                                        </div>
                                    </div>
                                    <div className='right'>
                                        <div className='section'>
                                            <div className='times'>
                                                {element.seance.map((element1, idx1) => {
                                                    return (
                                                        <>
                                                        <input type='radio' onClick={()=>{setSelectedDate({date: element1.realDate, cinema: element.name})}} />
                                                        <label htmlFor={'time'+idx+idx1} onClick={()=>{setSelectedDate({date: element1.realDate, cinema: element.name})}} >
                                                            <b>{element1.type}</b>&nbsp;{element1.price}&nbsp;сом&nbsp;{stringifyTime(element1.realDate)}</label>
                                                        </>
                                                )})}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {showRender[element.name]?
                                <div className='schem-container'>
                                    <div className='change-place'>
                                        <div className='change-place_content'>
                                            {seance!==undefined&&seance.seats!==undefined?
                                            <Render seats={seance.seats} holderSeats={holderSeats} seance={seance}/>:null}
                                        </div>
                                        <div className='change-place_footer'>
                                            <div className='container wrap'>
                                                <div className='amount-ticket'>
                                                    Всего <br />
                                                    {cart.length} билетов
                                                </div>
                                                <div className='ticket'>
                                                    {cart.map((item) => {
                                                        return(
                                                            <div>
                                                                <span className='white'>{item.name}</span>
                                                            </div>
                                                        )
                                                    })}
                                                </div>
                                                <div className='price'>
                                                    <div>
                                                        Итого:
                                                        <span>{fullPrice}&nbsp;сом</span>
                                                    </div>
                                                </div>
                                                <div className='order-wrap'>
                                                    <a onClick={()=>{if(cart.length>0)handleShow()}} className='btn btn-primary order-btn'>Оформить заказ</a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className='order-form'>
                                        <form action='#'>
                                            <div className='ticket-information'>
                                                <div className='container'>
                                                    <div className='row'><div className='col-12'><span className='back-to-schem'>Назад</span></div></div>
                                                    <div className='row'>
                                                        <div className='col-4'>
                                                            <div className='ticket_img'>
                                                                <img src='img/film-detail1.jpg' alt />
                                                            </div>
                                                        </div>
                                                        <div className='col-3 ticket_info-wrap'>
                                                            <div className='ticket_info'>
                                                                <div className='ticket_title'>Ёлки Последние</div>
                                                                <div className='ticket_date'>6 января, вс, 11:00
                                                                    <br />
                                                                    Зал 7
                                                                </div>
                                                                <div className='ticket_labels'>
                                                                    <div className='label'>Обычные сеансы 2D</div>
                                                                    <div className='label'>6+</div>
                                                                </div>
                                                                <div className='ticket_place'>
                                                                    <div className='column'>ряд <span className='white'>2</span></div>
                                                                    <div className='place'>место <span className='white'>8</span></div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className='col-5'>
                                                            <div className='ticket_form'>
                                                                <div className='form-group name'>
                                                                    <input type='text' className='form-control' placeholder='Фамилия Имя' aria-required='true' />
                                                                    <div className='help-block' />
                                                                </div>
                                                                <div className='email'>
                                                                    <div className='form-group name'>
                                                                        <input type='text' className='form-control' placeholder='Email' aria-required='true' />
                                                                        <div className='help-block' />
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className='change-place_footer info_footer'>
                                                    <div className='container wrap'>
                                                        <div className='info'>
                                                            Нажимая кнопку «Оплатить», вы соглашаетесь с <a href='http://kassir.kg/document/публ_оферта.docx' download target="_blank">условиями договора оферты</a>.
                                                        </div>
                                                        <div className='price'>
                                                            <div>
                                                                Итого:
                                                                <span>1740 руб.</span>
                                                            </div>
                                                        </div>
                                                        <div className='order-wrap'>
                                                            <a className='btn btn-primary order-btn' onClick={show}>Оплатить</a>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                                :null}
                            </>
                        )})}
                </div>
                {show?
                    <div className='lds-div1'>
                        <div onclick={()=>{}} style={{ background: 'white', padding: '25px', zIndex: '3000', maxWidth: '600px'}}>
                            <div className='event-content_info'>
                                <div className='date-dropdown'>
                                    <div className='input-group'>
                                        <select onChange={handleMerchant} className='custom-select'>
                                            <option selected disabled hidden>Выбрать способ оплаты</option>
                                            <option>ASISNUR</option>
                                            <option>М-bank</option>
                                            <option>Balance.kg</option>
                                            <option>ЭЛСОМ</option>
                                            <option>Visa/Maestro</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                            <br/>
                            {merchant==='Visa/Maestro'?
                                <>
                                <fieldset className='fields-agreement'>
                                    <fieldset className='fields-contacts'>
                                        <legend>{lang==='ru'? 'Контактные данные' : 'Байланыш маалыматтары'}</legend>
                                        <div className='form-group'>
                                            <input value={name} onChange={handleName} className='form-control' type='text' placeholder={lang==='ru'? 'Имя' : 'Ысым'} />
                                        </div>
                                        <div className='form-group'>
                                            <input value={email} onChange={handleEmail} className='form-control' type='text' placeholder='Email' />
                                        </div>
                                        <label htmlFor='flat-checkbox-1' className='icheck'>
                                            Формат: +996705356742
                                        </label>
                                        <div className='form-group'>
                                            <input value={phone} onChange={handlePhone} className='form-control' type='text' placeholder={lang==='ru'? 'Номер телефона' : 'Телефон номуру'} />
                                        </div>
                                    </fieldset>
                                </fieldset>
                                <form method='post' action='https://entegrasyon.asseco-see.com.tr/fim/est3Dgate'>
                                    <center>
                                        <table className='tableClass'>
                                            <tbody>
                                            <tr>
                                                <td align='center' colSpan={2}>
                                                    {
                                                        name.length>0&&email.length>0&&phone.length>0&&validMail(email)&&validPhone(phone)?
                                                            <input onClick={buy} type='submit' defaultValue='Submit' className='buttonClass' />
                                                            :
                                                            <b>Неверные данные</b>
                                                    }
                                                </td>
                                            </tr>
                                            </tbody></table>
                                        <input type='hidden' name='clientid' defaultValue='170000005' />
                                        <input type='hidden' name='amount' defaultValue={fullPrice} />
                                        <input type='hidden' name='islemtipi' defaultValue='Auth' />
                                        <input type='hidden' name='taksit' defaultValue='' />
                                        <input type='hidden' name='oid' defaultValue={wallet} />
                                        <input type='hidden' name='okUrl' defaultValue='https://kassir.kg/payment/visa/pay' />
                                        <input type='hidden' name='failUrl' defaultValue='https://kassir.kg/payment/tested' />
                                        <input type='hidden' name='rnd' defaultValue={'daseron'} />
                                        <input type='hidden' name='hash' defaultValue={visaCode} />
                                        <input type='hidden' name='storetype' defaultValue='3d_pay_hosting' />
                                        <input type='hidden' name='lang' defaultValue='ru' />
                                        <input type='hidden' name='currency' defaultValue='417' />
                                        <input type='hidden' name='refreshtime' defaultValue={0} />
                                        <br/>
                                    </center>
                                </form>
                                </>
                                :null}
                            {merchant==='М-bank'?
                                <>
                                <fieldset className='fields-agreement'>
                                    <fieldset className='fields-contacts'>
                                        <legend>{lang==='ru'? 'Контактные данные' : 'Байланыш маалыматтары'}</legend>
                                        <div className='form-group'>
                                            <input value={name} onChange={handleName} className='form-control' type='text' placeholder={lang==='ru'? 'Имя' : 'Ысым'} />
                                        </div>
                                        <div className='form-group'>
                                            <input value={email} onChange={handleEmail} className='form-control' type='text' placeholder='Email' />
                                        </div>
                                        <label htmlFor='flat-checkbox-1' className='icheck'>
                                            Формат: +996705356742
                                        </label>
                                        <div className='form-group'>
                                            <input value={phone} onChange={handlePhone} className='form-control' type='text' placeholder={lang==='ru'? 'Номер телефона' : 'Телефон номуру'} />
                                        </div>
                                    </fieldset>
                                    <div className='checkbox icheck form-group'>
                                        <label htmlFor='flat-checkbox-1' className='icheck'>
                                            <b>Оплатите билет в течение часа.</b> Введите номер своего счёта <b>{wallet}</b>, внесите сумму оплаты <b>{fullPrice}</b> и нажмите «Оплатить».
                                        </label>
                                    </div>
                                </fieldset>
                                <div className='form-group field-button'>
                                    <input className='btn btn-primary' defaultValue='Принять' onClick={()=>{
                                        if(name.length>0&&email.length>0&&phone.length>0) {
                                            if(validMail(email)&&validPhone(phone))buy()
                                            else alert('Неверные данные!!!')
                                        } else alert('Заполните все данные!!!')
                                    }} />
                                    <br/>
                                    <br/>
                                    <input className='btn btn-primary' defaultValue='Отмена' onClick={()=>{handleShow()}} />
                                </div>
                                </>
                                :null}
                            {merchant==='ASISNUR'?
                                <>
                                <fieldset className='fields-agreement'>
                                    <fieldset className='fields-contacts'>
                                        <legend>{lang==='ru'? 'Контактные данные' : 'Байланыш маалыматтары'}</legend>
                                        <div className='form-group'>
                                            <input value={name} onChange={handleName} className='form-control' type='text' placeholder={lang==='ru'? 'Имя' : 'Ысым'} />
                                        </div>
                                        <div className='form-group'>
                                            <input value={email} onChange={handleEmail} className='form-control' type='text' placeholder='Email' />
                                        </div>
                                        <label htmlFor='flat-checkbox-1' className='icheck'>
                                            Формат: +996705356742
                                        </label>
                                        <div className='form-group'>
                                            <input value={phone} onChange={handlePhone} className='form-control' type='text' placeholder={lang==='ru'? 'Номер телефона' : 'Телефон номуру'} />
                                        </div>
                                    </fieldset>
                                    <div className='checkbox icheck form-group'>
                                        <label htmlFor='flat-checkbox-1' className='icheck'>
                                            <b>Оплатите билет в течение часа.</b> На главном экране терминала выберите раздел «Интернет Магазины», затем выберите «KASSIR.KG». Далее введите номер своего счёта <b>{wallet}</b>, внесите сумму оплаты <b>{fullPrice}</b> и нажмите «Оплатить».
                                        </label>
                                    </div>
                                </fieldset>
                                <div className='form-group field-button'>
                                    <input className='btn btn-primary' defaultValue='Принять' onClick={()=>{
                                        if(name.length>0&&email.length>0&&phone.length>0) {
                                            if(validMail(email)&&validPhone(phone))buy()
                                            else alert('Неверные данные!!!')
                                        } else alert('Заполните все данные!!!')
                                    }} />
                                    <br/>
                                    <br/>
                                    <input className='btn btn-primary' defaultValue='Отмена' onClick={()=>{handleShow()}} />
                                </div>
                                </>
                                :null}
                            {merchant==='ЭЛСОМ'?
                                <>
                                <fieldset className='fields-contacts'>
                                    <fieldset className='fields-contacts'>
                                        <legend>{lang==='ru'? 'Контактные данные' : 'Байланыш маалыматтары'}</legend>
                                        <div className='form-group'>
                                            <input value={name} onChange={handleName} className='form-control' type='text' placeholder={lang==='ru'? 'Имя' : 'Ысым'} />
                                        </div>
                                        <div className='form-group'>
                                            <input value={email} onChange={handleEmail} className='form-control' type='text' placeholder='Email' />
                                        </div>
                                        <label htmlFor='flat-checkbox-1' className='icheck'>
                                            Формат: +996705356742
                                        </label>
                                        <div className='form-group'>
                                            <input value={phone} onChange={handlePhone} className='form-control' type='text' placeholder={lang==='ru'? 'Номер телефона' : 'Телефон номуру'} />
                                        </div>
                                    </fieldset>
                                </fieldset>
                                <fieldset className='fields-change-pass'>
                                    <b>Оплатите билет в течение получаса.</b> Ваш одноразовый код для оплаты через кошелек "Элсом": <div style={{color: 'red'}}>{elsomCode}</div><br/>
                                    Оплатите по данному реквизиту в Личном кабинете "Элсом" (web.elsom.kg) или скачайте обновленное приложение в Google Play по ссылке ЭЛСОМ 2.0.
                                    Номер счета необходимо ввести в пункте «Разовый код оплаты».
                                </fieldset>
                                <div className='form-group field-button'>
                                    <input className='btn btn-primary' defaultValue='Принять' onClick={()=>{ if(name.length>0&&email.length>0&&phone.length>0) {
                                        if(validMail(email)&&validPhone(phone))buy()
                                        else alert('Неверные данные!!!')
                                    } else alert('Заполните все данные!!!')
                                    }} />
                                </div>
                                <div className='form-group field-button'>
                                    <input className='btn btn-primary' defaultValue='Отмена' onClick={()=>{handleShow()}} />
                                </div>
                                </>
                                :null
                            }
                            {merchant==='Balance.kg'?
                                <>
                                <fieldset className='fields-contacts'>
                                    <fieldset className='fields-contacts'>
                                        <legend>{lang==='ru'? 'Контактные данные' : 'Байланыш маалыматтары'}</legend>
                                        <div className='form-group'>
                                            <input value={name} onChange={handleName} className='form-control' type='text' placeholder={lang==='ru'? 'Имя' : 'Ысым'} />
                                        </div>
                                        <div className='form-group'>
                                            <input value={email} onChange={handleEmail} className='form-control' type='text' placeholder='Email' />
                                        </div>
                                        <label htmlFor='flat-checkbox-1' className='icheck'>
                                            Формат: +996705356742
                                        </label>
                                        <div className='form-group'>
                                            <input value={phone} onChange={handlePhone} className='form-control' type='text' placeholder={lang==='ru'? 'Номер телефона' : 'Телефон номуру'} />
                                        </div>
                                    </fieldset>
                                </fieldset>
                                <div className='form-group field-button'>
                                    <input className='btn btn-primary' defaultValue='Принять' onClick={()=>{ if(name.length>0&&email.length>0&&phone.length>0) {
                                        if(validMail(email)&&validPhone(phone))buy()
                                        else alert('Неверные данные!!!')
                                    } else alert('Заполните все данные!!!')
                                    }} />
                                </div>
                                <div className='form-group field-button'>
                                    <input className='btn btn-primary' defaultValue='Отмена' onClick={()=>{handleShow()}} />
                                </div>
                                </>
                                :null
                            }
                            {merchant===''?
                                <div className='form-group field-button'>
                                    <input className='btn btn-primary' defaultValue='Отмена' onClick={()=>{handleShow()}} />
                                </div>
                                :null}
                        </div>
                    </div>
                    :null}
            </div>
            </>
        );
    }
)

function mapStateToProps (state) {
    return {
        user: state.user,
        app: state.app
    }
}

function mapDispatchToProps(dispatch) {
    return {
        appActions: bindActionCreators(appActions, dispatch),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Event);
